video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

hero-container {
    /* background: url('images/img-home.jpg') center center/cover no-repeat; */
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
}

.hero-container > h1 {
    color: #fff;
    font-size: 100px;
    text-align: center;
    padding-top: 15%;
  }

.hero-container > p {
  margin-top: 8px;
  color: #fff;
  font-size: 32px;
  text-align: center;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

.hero-btns {
    text-align: center;
    padding: 40px;
}

.hero-btns .btn {
  margin: 6px;
}

@media screen and (max-width: 960px){
  .hero-container > h1 {
    font-size: 70px;
    text-align: center;
    padding-top: 15%;
  }

  .hero-container > p {
    margin-top: 8px;
    font-size: 25px;
    text-align: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  }
}