.navbar {	
    background: linear-gradient(180deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);	
    height: 60px;	
    display: flex;	
    justify-content: center;	
    align-items: center;	
    font-size: 1.2rem;	
    position: -webkit-sticky;	
    position: sticky;	
    top: 0;	
    z-index: 999;	
    font-family: 'Lato', sans-serif;
  }
  
  .fab{
    color: rgb(206, 206, 206);
  }
  
  .navbar-container {	
    display: flex;	
    justify-content: center;	
    align-items: center;	
    height: 80px;	
    max-width: 1500px;	
  }	
  
  .navbar-logo {	
    color: #fff;	
    justify-self: start;	
    margin-left: 20px;	
    cursor: pointer;	
    text-decoration: none;	
    font-size: 25px;	
    display: flex;	
    align-items: center;	
  }	
  
  .fa-typo3 {	
    margin-left: 0.5rem;	
    font-size: 1.8rem;	
  }	
  
  .nav-menu {	
    display: grid;	
    grid-template-columns: repeat(4, auto);	
    list-style: none;	
    text-align: center;	
    width: 40vw;	
    justify-content: end;	
    margin-right: 2rem;	
  }	
  
  .nav-item {	
    height: 60px;	
  }	
  
  .nav-links {	
    color: #fff;	
    display: flex;	
    align-items: center;	
    text-decoration: none;	
    padding: 0.5rem 1rem;	
    height: 60px;	
  }	
  
  .nav-links:hover {	
    border-bottom: 4px solid #fff;	
    transition: all 0.2s ease-out;	
  }	
  
  .fa-bars {	
    color: #fff;	
  }	
  
  .nav-links-mobile {	
    display: none;	
  }	
  
  .menu-icon {	
    display: none;	
  }	
  
  .is-active{
    border-bottom: 4px solid #fff;	
  }
  
  @media screen and (max-width: 960px) {	
    .NavbarItems {	
      position: relative;	
    }
  
    .nav-menu {	
      display: flex;	
      flex-direction: column;	
      width: 100%;	
      height: 100vh;	
      position: absolute;	
      top: 60px;	
      left: -100%;	
      opacity: 1;	
      transition: all 0.5s ease;	
      font-family: 'Lato', sans-serif;
    }	
  
    .nav-menu.active {	
      background: #242222;	
      left: 0px;	
      opacity: 1;	
      transition: all 0.5s ease;	
      z-index: 1;	
      padding: 0;
    }	
  
    .nav-links {	
      text-align: center;	
      width: 100%;	
      display: table;	
      padding: 15px;
      justify-content: center;
    }	
  
    .nav-links:hover {	
      background-color: #fff;	
      color: #242424;	
      border-radius: 0;	
    }	
  
    .navbar-logo {	
      position: absolute;	
      top: 0;	
      left: 0;	
      transform: translate(25%, 40%);	
    }	
  
    .menu-icon {	
      display: block;	
      position: absolute;	
      top: 0;	
      right: 0;	
      transform: translate(-100%, 30%);	
      font-size: 1.8rem;	
      cursor: pointer;	
    }	
  
    .fa-times {	
      color: #fff;	
      font-size: 2rem;	
    }	
  
    .nav-links-mobile {	
      display: block;	
      text-align: center;	
      margin: 2rem auto;	
      border-radius: 4px;	
      width: 80%;	
      text-decoration: none;	
      font-size: 1.5rem;	
      background-color: transparent;	
      color: #fff;	
      padding: 14px 20px;	
      border: 1px solid #fff;	
      transition: all 0.3s ease-out;	
    }	
  
    .nav-links-mobile:hover {	
      background: #fff;	
      color: #242424;	
      transition: 250ms;	
    }	
  }
:root{
    --primary: #fff;
}

btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary{
    background-color: #fff;
    background-color: var(--primary);
    color: #242424;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
}

.btn--primary:hover{
    background-color: transparent;
    color: #fff;
}

.btn--outline{
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--outline:hover{
    background-color: #fff;
    color: #242424;
}

.btn-medium{
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large{
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover{
    transition: all 0.3s ease-out;
}
